(() => {
  const button = document.querySelector(`.nav__button`);
  const nav = document.querySelector(`.nav`);
  const list = document.querySelector(`.nav__list`);

  if (window.matchMedia(`(max-width: 1024px)`).matches) {
    list.inert = true;
  }

  // Inert для списка
  window.addEventListener(`resize`, () => {
    if (window.matchMedia(`(max-width: 1024px)`).matches) {
      list.inert = true;
    } else {
      list.inert = false;
    }
  });

  // Закрыть клавишей ESC
  function onNavEscPress(e) {
    if (e.key === `Escape`) {
      e.preventDefault();
      closeNav();
    }
  }

  // Закрыть кликом вне списка
  function onNavClose(e) {
    if (e.target !== button) {
      closeNav();
    }
  }

  // Открыть/Закрыть навигацию
  function toggleMenu(remove, add, bollean, evt) {
    nav.classList.remove(remove);
    nav.classList.add(add);
    list.inert = bollean;
    evt(`click`, onNavClose);
    evt(`keydown`, onNavEscPress);
  }

  const openNav = () => toggleMenu(`nav--closed`, `nav--opened`, false, document.addEventListener);
  const closeNav = () => toggleMenu(`nav--opened`, `nav--closed`, true, document.removeEventListener);

  button.addEventListener(`click`, (e) => {
    e.preventDefault();
    if (nav.classList.contains(`nav--closed`)) {
      openNav();
    } else if (nav.classList.contains(`nav--opened`)) {
      closeNav();
    }
  });
})();
